body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #fff;
  text-align: center;
  min-height: 100%;
  width: 100%;
}
.wallpaper{
  width: 100%;
  height: 100%;
  text-align: center;
  position: fixed;
  z-index: -10;
}
.wallpaper-img{
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.container{
  width: 90%;
  min-width: 400px;
  max-width: 800px;
  margin: auto;
  background: #2b364791;
  height: 100%;
  padding: 30px 0 50px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



